<template>
  <div class="setting">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-6">
          <b-form class="p-3">
            <b-form-group id="input-group-2" label="私钥" label-for="input-1" :description="hideAccount">
              <b-form-input
                  id="input-1"
                  v-model="privateKey"
                  @input="getAccount"
                  required
                  placeholder="开奖者钱包私钥"
                  style="width: 10rem"
              ></b-form-input>
            </b-form-group>

            <b-form-group id="input-group-2" label="兑换比例" label-for="input-2" description="默认100">
              <b-form-input
                  id="input-2"
                  v-model="rate"
                  required
                  placeholder="兑换比例"
              ></b-form-input>
              <b-button type="button" variant="primary" @click="changeRate">设置</b-button>
            </b-form-group>

            <b-form-group id="input-group-2" label="手续费" label-for="input-3" description="百分比">
              <b-form-input
                  id="input-3"
                  v-model="fee"
                  required
                  placeholder="手续费"
              ></b-form-input>
              <b-button type="button" variant="primary" @click="changeFee">设置</b-button>
            </b-form-group>

<!--            <b-form-group id="input-group-4" label="回合时间" label-for="input-4" description="分钟">-->
<!--              <b-form-input-->
<!--                  id="input-4"-->
<!--                  v-model="interval"-->
<!--                  required-->
<!--                  placeholder="回合时间"-->
<!--              ></b-form-input>-->
<!--              <b-button type="button" variant="primary" @click="changeFee">设置</b-button>-->
<!--            </b-form-group>-->
          </b-form>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import Data from "@/components/Contract";

export default {
  name: "Manager",
  data() {
    return {
      account: '',
      privateKey: '',
      contractAddress: Data.btcAddress,
      btcContract: new web3.eth.Contract(Data.btcAbi, Data.btcAddress),
      tokenContract: new web3.eth.Contract(Data.tokenAbi, Data.tokenAddress), //代币合约对象
      rate: '',
      fee: '',
      interval: 3
    }
  }
  ,created() {

  },
  methods: {
    getAccount() {
      if (this.privateKey === '') return;
      let obj = web3.eth.accounts.privateKeyToAccount('0x' + this.privateKey);
      this.account = obj.address
      this.getBNB()

      localStorage.setItem('privateKey', this.privateKey)
    },
    changeFee()
    {
      if (!this.checkAccount()) return false;
      let _fee = this.fee / 100 * 10000
      this.tokenContract.methods.changeFee(_fee).send({from: this.account}).then(res => {
        alert('设置成功')
      }).error(err => {
        alert('设置失败')
      })
    },
    changeRate(){
      if (!this.checkAccount()) return false;
      this.tokenContract.methods.changeRate(this.rate).send({from: this.account}).then(res => {
        alert('设置成功')
      }).error(err => {
        alert('设置失败')
      })
    },
    changeInterval()
    {
      if (!this.checkAccount()) return false;
    },
    checkAccount()
    {
      if (this.account === '') {
        alert('账户为空！')
        return false;
      }
      return true;
    }
  },
  computed: {
    hideAccount() {
      if (this.account === '') return ''
      return this.account.substr(0, 6) + '...' + this.account.substr(-4, 4)
    }
  }
}
</script>

